import React from "react";

const Dashboard = () => {
  return (
    <div style={{ padding: "2rem" }}>
      <h2>Theatre Owner Dashboard</h2>
      <p>Welcome to your dashboard! Here you can manage cinemas, movies, and bookings.</p>
      <ul>
        <li>View list of your theatres</li>
        <li>Add or remove movies</li>
        <li>Track bookings and payments</li>
      </ul>
    </div>
  );
};

export default Dashboard;
